export default function App() {
  const [playerData, setPlayerData] = useState(null);
  const [err, setErr] = useState();
  const [fifa, setFifa] = useState(22);
  const [console, setConsole] = useState('ps');
  const [partnerId, setPartnerId] = useState(28195);
  const [secretKey, setSecretKey] = useState(
    'b3d9ecfdf1b7e027021ad6c7b6e3a787'
  );
  const [timeStamp, setTimeStamp] = useState(
    Math.round(new Date().getTime() / 1000)
  );
  const [minBuy, setMin] = useState(10000);
  const [maxBuy, setMaxBuy] = useState(300000);
  const signature = md5(partnerId + secretKey + timeStamp);

  useEffect(() => {
    setInterval(() => {
      setTimeStamp(Math.round(new Date().getTime() / 1000));
    }, 1000);
  }, []);

  useEffect(() => {
    fetch(
      `https://dsfut.net/api/22/ps/${partnerId}/${timeStamp}/${signature}?min_buy=${minBuy}&max_buy=${maxBuy}`
    )
      .then((res) => res.json())
      .then((data) => setPlayerData(data))
      .catch((err) => setErr(err));
  }, [timeStamp]);

  return (
    <div>
      {err && <h1>{err && err.message}</h1>}
      {playerData && <h1>{playerData.player.rating}</h1>}
      {playerData && <h1>{playerData.message}</h1>}
    </div>
  );

  // return (
  //   <QueryClientProvider client={queryClient}>
  //     <Example />
  //   </QueryClientProvider>
  // );
}

